import React from 'react'
import { format } from 'date-fns';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

import { FaRegClock, FaCalendarDay } from 'react-icons/fa';

export default function Clock(props) {

    const [time, setTime] = React.useState(format(Date.now(), "h:mm a"))

    React.useEffect(() => {
        let mounted = false;
        let interval = null;

        if (!mounted) {
            interval = setInterval(() => {
                setTime(format(Date.now(), "h:mm a"));
            }, 1000);
        }

        return () => {
            mounted = false;
            clearInterval(interval);
        }
    }, [setTime])

    return (
        <Box sx={{ display: 'flex', flexDirection: 'row' }}>
            {props.showToday &&
                <Typography
                    variant={props.variant ?? "h6"}
                    sx={{
                        "&:after": {
                            'content': '"|"',
                            ml: 1,
                            mr: 1
                        }
                    }}
                >
                    {props.showIcon &&
                        <FaCalendarDay style={{ marginRight: 5 }} size={18} />
                    }
                    {format(Date.now(), "MMMM dd, yyyy")}
                </Typography>
            }
            <Typography variant={props.variant ?? "h6"}>
                {props.showIcon &&
                    <FaRegClock style={{ marginRight: 5, marginTop: 2 }} size={18} />
                }
                {time}
            </Typography>
        </Box>
    )
}
