import React, { Suspense } from 'react';
import { Route, Switch } from 'react-router';
import { PageContext } from '../../../context/pageContext';

// Redux
import { useDispatch, useSelector } from 'react-redux';
import { logout } from '../../../redux/async/authAsync';
import { setIsBootstrapped } from '../../../redux/slices/systemSlice'; 

// Page Styling
import { styled } from '@mui/material/styles';
import myAvatar from '../../../../assets/temp/me.jpg';

// MUI Components
import Box from '@mui/material/Box';
import MuiDrawer from '@mui/material/Drawer';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Tooltip from '@mui/material/Tooltip'; 

// Page Components
import Copyright from '../../components/shared/Copyright/Copyright';
import SearchBox from '../../components/shared/SearchBox/SearchBox';
import Brand from '../../components/shared/Brand/Brand';
import NavItem from '../../components/nav/NavItem';
import NavCollapse from '../../components/nav/NavCollapse';
import Overlay from '../../components/shared/Overlay/Overlay';
import Messages from '../../components/shared/Messages/Messages';
import Notifications from '../../components/shared/Notifications/Notifications';
import Help from '../../components/shared/Help/Help';
import AccountMenu from '../../components/shared/Menu/AdminMenu';
import Clock from '../../components/shared/Clock/Clock';

// React-icons
import { MdExitToApp } from 'react-icons/md';

// Navigation Menu
import { menu } from '../../components/nav/config/admin';

// Page Containers
const Dashboard = React.lazy(() => import('../../components/pages/admin/AdminDashboard'));
const Members = React.lazy(() => import('../../components/pages/shared/MembersPage'));
const MemberProfile = React.lazy(() => import('../../components/pages/shared/MemberProfilePage'));
const Deposits = React.lazy(() => import('../../components/pages/shared/DepositsPage'));
const Withdrawals = React.lazy(() => import('../../components/pages/shared/WithdrawalsPage'));
const Users = React.lazy(() => import('../../components/pages/admin/UsersPage'));
const MasterfilesPage = React.lazy(() => import('../../components/pages/admin/MasterfilesPage'));
const UserProfile = React.lazy(() => import('../../components/pages/admin/UsersProfilePage'));
const Settings = React.lazy(() => import('../../components/pages/admin/AdminSettingsPage'));

const closedMixin = (theme) => ({
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: 'hidden',
  width: `calc(${theme.spacing(7)} + 1px)`,
  [theme.breakpoints.up('sm')]: {
    width: `calc(${theme.spacing(9)} + 1px)`,
  },
});

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
}));

const AppBar = styled(MuiAppBar, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open, drawerwidth }) => ({
    ...(!open && {
      marginLeft: 0,
      width: `calc(100% - ${drawerwidth}px)`,
    }),
  }));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open, drawerwidth }) => ({
    width: drawerwidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
    boxSizing: 'border-box',
    ...(!open && {
      ...closedMixin(theme),
      '& .MuiDrawer-paper': closedMixin(theme),
    }),
  }),
);

export default function Admin(props) {

  const dispatch = useDispatch();

  // ->> System Redux
  const systemRedux = useSelector((state) => state.system.value);
  const isBootstrapped = systemRedux.isBootstrapped;

  // ->> Auth Redux
  const authRedux = useSelector((state) => state.auth.value);

  const open = false;
  const drawerWidth = 70;

  const [searchOption, setSearchOption] = React.useState("Member");

  const handleLogout = () => {
    dispatch(logout());
    window.location.href = "/";
  }

  const handleSearch = (str) => {

    if (searchOption.toLowerCase() === "member") {
      console.log(str);
    }

  };

  const navigation = menu

  React.useState(() => {
    let mounted = false;

    if (!mounted) {
      // dispatch here
      if (!isBootstrapped) { 
        // do something during boot up
      }

    }

    return () => {
      mounted = true;
      // Bootstrap only once
      dispatch(setIsBootstrapped(true));
    }

  }, [dispatch, isBootstrapped])

  return (
    <PageContext.Provider value={{ open }}>
      <Box sx={{ display: 'flex', overflow: 'hidden' }}>
        <CssBaseline />
        <AppBar elevation={1} position="fixed" open={open} drawerwidth={drawerWidth}>
          <Toolbar>
            <Grid container>
              <Grid xs={3} item>
                <Box display="flex" justifyContent="flex-start">
                  <Box display="flex" flexDirection="row" alignItems="center" justifyContent="center" sx={{ mt: 2 }}>
                    <Clock showIcon={true} />
                  </Box>
                </Box>
              </Grid>
              <Grid xs={9} item>
                <Box display="flex" justifyContent="flex-end" sx={{ marginLeft: 20 }}>
                  <SearchBox option={searchOption} setOption={setSearchOption} handleSearch={handleSearch} />
                  <Messages />
                  <Notifications />
                  <Help />
                  <AccountMenu profile={myAvatar} />
                  <Tooltip arrow={true} title={"Logout?"} placement="bottom">
                    <IconButton color="inherit" style={{ backgroundColor: 'transparent' }} onClick={() => handleLogout()}>
                      <Typography>
                        Exit
                      </Typography>
                      <MdExitToApp size={20} style={{ marginLeft: 5 }} />
                    </IconButton>
                  </Tooltip>
                </Box>
              </Grid>
            </Grid>
          </Toolbar>
        </AppBar>
        <Drawer variant="permanent" open={open}>
          <DrawerHeader sx={{ backgroundColor: open && "#00BFA5" }}>
            <Brand open={open} width={drawerWidth} />
          </DrawerHeader>
          <Divider sx={{ mt: open ? 9.8 : 0 }} />
          <Box display="flex" flexDirection="column" justifyContent="space-between" flexGrow={1}>
            <List disablePadding={true}>
              <Box>
                {navigation.map((value, key) => {
                  if (!value.collapse) {
                    return <NavItem options={value} index={key} open={open} key={`ListItemMenu-${key}`} history={props.history} />
                  }
                  return <NavCollapse index={key} options={value} open={open} key={`ListItemMenu-${key}`} history={props.history} />

                })}
              </Box>
            </List>
            <List>
              {open && (
                <Box sx={{ ml: 1 }}>
                  <Copyright alignment="left" />
                </Box>
              )}
            </List>
          </Box>
        </Drawer>
        <Box component="main" sx={{ flexGrow: 1, p: 0, margin: 0, paddingLeft: `${open ? drawerWidth + 15 : 0}px`, overflow: 'hidden' }}>
          <DrawerHeader />
          <Grid container>
            <Grid item xs={12}>
              <Box square sx={{ m: 0, height: '100vh', overflow: 'hidden', backgroundColor: "#FFF" }}>
                <Suspense fallback={<Overlay isDrawerOpen={open} isLoading={true} zindex={2} opacity={0.5} />}>
                  <Switch>
                    <Route exact path="/admin">
                      <Dashboard user={authRedux} />
                    </Route>
                    <Route exact path="/members">
                      <Members />
                    </Route>
                    <Route exact path="/deposits">
                      <Deposits />
                    </Route>
                    <Route exact path="/withdrawals">
                      <Withdrawals />
                    </Route>
                    <Route exact path="/profile">
                      <MemberProfile />
                    </Route>
                    <Route exact path="/users">
                      <Users />
                    </Route>
                    <Route exact path="/admin/profile">
                      <UserProfile />
                    </Route>
                    <Route exact path="/admin/masterfiles">
                      <MasterfilesPage />
                    </Route>
                    <Route exact path="/admin/settings">
                      <Settings />
                    </Route>
                    <Route exact path="/admin/faq">
                      FAQs
                    </Route>
                    <Route exact path="/admin/support">
                      Support
                    </Route>
                  </Switch>
                </Suspense>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </Box >
    </PageContext.Provider>
  );
}
